.bestsellers{
    margin: 4rem 6rem;
}
.bestsellers h1{
    font-size: 3rem;
}
.first-bestseller{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 4.5rem;

}
.first-bestseller-reverse{
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    margin-top: 4.5rem;

}

.best-text{
 width:45%;
 text-align: center;
 font-size: 1.1rem; 
 
}

.best-text h2{
    padding-bottom: 1rem;
}

.best-img{
    position: relative;
    width: 50%;
    display: flex;
    justify-content: space-between;
    z-index: -99;
}

.best-img img{
    width: 49%;
    height: 350px;
    object-fit: cover;
    border-radius: 6px;
    box-shadow: -1px 1px 62px -18px rgba(0,0,0,0.19);
}

.best-img img:nth-child(2){
    position: absolute;
    top:-10%;
    right:0;
}
@media screen and (max-width:850px){
    .bestsellers{
    margin: 4rem 2rem;
}
.first-bestseller{
    display: flex;
    flex-direction: column-reverse;
    margin-top: 2.5rem;
}
.first-bestseller-reverse{
    display: flex;
    flex-direction: column-reverse;
     margin-top: 2.5rem;

}

.best-text{
    width:100;

}
.best-img{
    width: 100%;
    margin: 1.8rem 0;
}
.best-img img{
height: 250px;
}
}